    <span class="hljs-tag">&lt;<span class="hljs-name">bee-picker</span> <span class="hljs-attr">:scroll-dom</span>=<span class="hljs-string">'scrollDom'</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'date3'</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">'dateDisabled'</span> <span class="hljs-attr">:time-disabled</span>=<span class="hljs-string">'timeDisabled'</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'datetime'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">bee-picker</span>&gt;</span> <span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
