    <span class="hljs-tag">&lt;<span class="hljs-name">bee-picker</span> <span class="hljs-attr">:scroll-dom</span>=<span class="hljs-string">'scrollDom'</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'date5'</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">'dateDisabled'</span> <span class="hljs-attr">:time-disabled</span>=<span class="hljs-string">'timeDisabled'</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'rangetime'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">bee-picker</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">date2</span>: <span class="hljs-literal">null</span>,
