<h3>属性值</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>类型值</td>
<td>string</td>
<td>date,datetime,range,rangetime</td>
<td>date</td>
</tr>
<tr>
<td>lang</td>
<td>语言</td>
<td>string</td>
<td>zh_cn</td>
<td>zh_cn</td>
</tr>
<tr>
<td>format</td>
<td>显示的日期格式化参数</td>
<td>string</td>
<td>-</td>
<td>见下方</td>
</tr>
<tr>
<td>labelFormat</td>
<td>显示的日期的别名设置</td>
<td>function</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>日期不可用的设置</td>
<td>function</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>timeDisabled</td>
<td>时间不可用的设置</td>
<td>object</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>timeVisible</td>
<td>时间设置可见性</td>
<td>object</td>
<td>-</td>
<td>见下方</td>
</tr>
<tr>
<td>placeholder</td>
<td>占位符</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>quickBtns</td>
<td>快速选择按钮</td>
<td>array</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>quickBtnsType</td>
<td>快速选择按钮的类型</td>
<td>string</td>
<td>inner,outer</td>
<td>inner</td>
</tr>
<tr>
<td>maxDays</td>
<td>日期最大的选择天数</td>
<td>number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>scrollDom</td>
<td>跟随滚动的DOM节点</td>
<td>HTML DOM</td>
<td>-</td>
<td>document</td>
</tr>
</tbody>
</table>
<br>
<br>
<blockquote>
<p>当type为date或者datetime时， format默认值为 YYYY-MM-DD <br>
当type为range或者rangetime时， format默认值为 YYYY-MM-DD hh:mm:ss</p>
</blockquote>
<pre v-pre=""><code v-pre="" class="language-js"><span class="hljs-comment">// timeVisible 默认值</span>
{
  <span class="hljs-attr">hour</span>: <span class="hljs-literal">true</span>,
